.movie-card .figure-caption{
    opacity: 1;
	transform: translateY(0px);
}
.figure-caption{
    position: relative;
	z-index: 3;
	color: white;
	opacity: 0;
	transform: translateY(30px);
	transition: 0.5s;
    display: none;
}
.figure-caption::before{
    /* content: "" ; */
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	border-radius: 15px;
	background: rgba(black, 0.6);
	z-index: 2;
	transition: 0.5s;
	opacity: 0;
}
