.bloc-tabs {
    display: flex;
  }
  .tabs {
    padding: 15px;
    text-align: center;
    width: 50%;
    background: rgba(128, 128, 128, 0.075);
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.274);
    box-sizing: content-box;
    position: relative;
    outline: none;

    color: #999;
    line-height: 30px;
    font-size: 16px;
    font-weight: bold;
    font-family: 'Arimo', sans-serif;
  }
  .active-tabs  {
    background: white;
    color: #6ac045;
    border-bottom: 1px solid transparent;
    border-radius: 10px 10px;
    line-height: 30px;
    font-size: 16px;
    font-weight: bold;
    font-family: 'Arimo', sans-serif;
  }
  .active-tabs::before {
    content: "";
    display: block;
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 5px;
    background: rgb(88, 147, 241);
  }
  button {
    border: none; 
  }
  .content-tabs {
    flex-grow : 1;
  }
  .content {
    background: white;
    padding: 20px;
    display: none;
  }
  .content p {
    color: #666666 ;
    font-style: italic;
    font-size: 0.85em;
    padding-bottom: 10px;
  }
  .content .forgot-psw{
    text-decoration: none; 
    margin-left:15rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding:1rem;
    padding-bottom: 10px;
  }
  .content .termsandcondin{
    text-decoration: none;
  }
  .active-content {
    display: block;
  }
  .submit-btn{
    border-radius: 4px;
    border: 0;
    width: 95%;
    position: static;
    height: 50px;
    background: #6ac045;
    font-weight: bold;
    color: #fff;
    top: 35px;
    font-size: 16px;
    padding:  0 20px 0 10px;
    margin-left: 13px;
    margin-right: 10px;
    margin-bottom: 20px;
}
