.pagination {
    width: 96%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    /* background-color: rgb(29, 28, 28); */
    padding-bottom: 20px;
  }
  .prev {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 2px 2px;
    width: 80px;
    height: 30px;
    border: none;
    /* margin-left: -20px; */
    border-radius: 4px;
    color: rgb(0, 0, 0);
    font-weight: 500;
    font-size: 16px;
  }
  .next {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 2px 2px;
    width: 80px;
    height: 30px;
    border: none;
    border-radius: 4px;
    color: black;
    font-weight: 500;
    font-size: 16px;
    margin-right: 40px;
  }